export default [{
    path: "/err",
    name: "err",
    component: () =>
      import ("@/pages/error/error"),
    meta: {
      title: '页面访问错误'
    },
  },
  {
    path: "*",
    redirect: "/err",
    meta: {
      title: '页面访问错误'
    },
  }
]



